import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back
    },
    data () {
        return {
            dShow:false,
            AppointmentList:[],
            HospitalName:this.$store.state.saveVipData.Hospital.HospitalName,
            index: null,
            itemShow:false,
            itemList:[],
            item:[],
            HospitalList:[],
            DepartmentList:[], 
            areaList:[],
            num:0,
            DepartmentId:this.$store.state.saveVipData&&this.$store.state.saveVipData.Department?this.$store.state.saveVipData.Department.DepartmentId:0,
            vipList:this.$store.state.saveVipData.vipList,
            ListDetails:this.$store.state.preList,
        }
    },
    created() {
        this.getArea()
        this.getAppointment(this.DepartmentId,this.$store.state.saveVipData.Department)
    },
    methods: {
        dDialog(){
            this.dShow = this.dShow?false:true
        },
        getArea(){
            this.$get('AppointmentAPI/Appointment/GetApHospitalCity',{
            }).then( m => {
                const arr = []
                m.forEach(v => {
                    arr.push(v.CityName)
                })
                arr.unshift('全部地区')
                this.itemList = [arr]
                this.areaList = m
                let CityId = 0
                if(this.$store.state.saveVipData && this.$store.state.saveVipData.CityName){
                    this.areaList.forEach(v =>{
                        if(v.CityName === this.$store.state.saveVipData.CityName){
                            this.item[0] = v.CityName
                            CityId = v.CityId
                        }
                    })
                }else{
                    this.item[0] = '全部地区'
                    CityId = 0
                }
                this.getHospital(CityId)
            })
        },
        getHospital(CityId){
            this.$post('AppointmentAPI/Appointment/QueryApHospitalPage',{
                HospitalName:'',
                CityId,
                AppointmentTypeId:this.$store.state.saveVipData.AppointmentTypeId,
                PageIndex:1,
                PageSize:100,
            }).then(o => {
               this.HospitalList = o.ReturnData
               if(this.$store.state.saveVipData && this.$store.state.saveVipData.Hospital){
                   this.getDepartment(this.$store.state.saveVipData.Hospital)
               }else{
                    this.getDepartment(this.HospitalList[0])
               }
            })
        },
        getDepartment(i,idx){
            if(!i){
                this.DepartmentList = []
                return
            }
            this.HospitalList.forEach((v,k)=>{
                if(i.HospitalId === v.HospitalId){
                    this.num = k
                    this.$store.state.saveVipData.Hospital = i
                    this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
                }
            })
            this.$post('AppointmentAPI/Appointment/QueryApDepartmentPage',{
                HospitalId:i.HospitalId,
                HospitalName:i.HospitalName,
                PageIndex:1,
                PageSize:100,
            }).then(o => {
                this.DepartmentList = o.ReturnData
            })
        },
        getAppointment(DepartmentId,i){
            this.DepartmentId = DepartmentId
            this.$store.state.saveVipData.Department = i
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
            this.dShow = false
            this.$get('AppointmentAPI/Appointment/QueryAppointmentPrices',{
                DepartmentId,
            }).then(o => {
                this.AppointmentList = o
            })
        },
        changeVal(){
            let a = 0
            let CityId = 0
            this.areaList.forEach(v =>{
                if(v.CityName === this.item[0]){
                    CityId = v.CityId
                    a = 1
                }
            })
            this.getHospital(CityId)
            this.$store.state.saveVipData.Hospital = null
            this.$store.state.saveVipData.CityName = this.item[0]
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
        },
        goAllGoods(i){
            let spa = this.vipList
            spa.GoodsNum = 1
            spa.isVip = 2
            spa.GoodsId = i.ApPriceId
            spa.DrPositionName = i.DrPositionName
            spa.FirstTypeName = i.FirstTypeName
            spa.SecondTypeName = i.SecondTypeName
            spa.HospitalName = i.HospitalName
            spa.DepartmentName = i.DepartmentName
            spa.IsAppointment = i.IsAppointment
            spa.VipPrice = i.AppointmentPrice
            spa.RecommondAmount = i.RecomAmount

            if(this.ListDetails && this.ListDetails.length){
                let t = 0
                this.ListDetails.forEach((v,k) => {
                    if(v.GoodsSource == this.vipList.GoodsSource && v.SpecialistGoodsId == this.vipList.SpecialistGoodsId && v.SpecialistHosGroupId == this.vipList.SpecialistHosGroupId && v.GoodsId == this.vipList.GoodsId){
                        this.ListDetails.splice(k, 1, spa)
                        t = 1
                    }
                })
                if(t == 0){
                    this.ListDetails.push(spa)
                }
            }else{
                this.ListDetails.push(spa)
            }
            this.$store.dispatch('pushnavNum',1)
            //this.$store.dispatch('pushIsFresh', true)
            this.$store.dispatch('pushpreList',this.ListDetails)
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
            this.$router.push('/allGoods')
        }

    },
}



